import "./index.css"
import { Link, useNavigate } from "react-router-dom"
import { useEffect, useState } from "react"
import Axios from "../../api/Axios";
import trackUserEvent from "../../helpers/trackUserEvent";

const axios = new Axios();

const Addresses = () => {

  const [billingAddress, setBillingAddress] = useState([])
  const [shippingAddress, setShippingAddress] = useState([])
  const [noAddressAvailable, setNoAddressAvailable] = useState(false)
  const navigate = useNavigate();

  const getUserAddressDetails = async () => {
    const options = {
      method: "GET"
    }
    const result = await axios.callApi(`user/getAddresses`, options)
    if (result.status == 200) {
      setBillingAddress(result?.data?.data?.filter(address => address.addressType == "billing"))
      setShippingAddress(result?.data?.data?.filter(address => address.addressType == "shipping"))
      if(result?.data?.data.length == 0){
        setNoAddressAvailable(true)
      }
    }
  }

  const handleAddAddressClick = (data) => {
    navigate(`/my-account/addresses/${data}-address${noAddressAvailable ? "?firstAddress=true" : ""}`)
  }

  useEffect(() => {
    getUserAddressDetails()
  }, [])

  useEffect(() => {
    trackUserEvent("View Addresses")
  },[])

  return (
    <div style={{ backgroundColor: "white" }}>
      <span className="text-style">
        The following addresses will be used on the checkout page by default.
      </span>
      <div className="container-fluid" style={{ backgroundColor: "white" }}>
        <div className="row" style={{ marginTop: "25px" }}>

          <div className="col-sm-12 col-lg-6 text-style" >
            <span style={{ fontSize: "30px" }}>Billing address</span><br />
           {billingAddress.map((item)=>{
            return (
              <div className="card p-2 mt-2">
              <div className="text-bold-md">
                <i className="fw-bold">{`${item?.firstName} ${item?.lastName}`}</i><br />
                <i>{`${item?.streetAddress}`}</i><br />
                {item?.landmark && <><i>{`${item?.landmark}`}</i><br /></>}
                <i>{`${item?.city} ${item?.pincode}`}</i><br />
                <i>{`${item?.state}`}</i>
                </div>
                <br />
               <Link className="inline-block" to={`/my-account/addresses/billing-address?id=${item.id}`} style={{ textDecoration: "none", color: "#777777" }}>{"Edit"}</Link>
              </div>
            )
            })}
          <button className="btn btn-dark mt-2" data-bs-toggle="modal" data-bs-target="#addAddress" onClick={()=>handleAddAddressClick('billing')}>Add New Address</button>
          </div>

          <div className="col text-style"  >
            <span style={{ fontSize: "30px" }}>Shipping address</span><br />
            {shippingAddress.map((item)=>{
            return (
              <div className="card p-2 mt-2">
              <div className="text-bold-md">
                <i className="fw-bold">{`${item?.firstName} ${item?.lastName}`}</i><br />
                <i>{`${item?.streetAddress}`}</i><br />
                {item?.landmark && <><i>{`${item?.landmark}`}</i><br /></>}
                <i>{`${item?.city} ${item?.pincode}`}</i><br />
                <i>{`${item?.state}`}</i>
                </div>
                <br />
               <Link className="d-inline" to={`/my-account/addresses/shipping-address?id=${item.id}`} style={{ textDecoration: "none", color: "#777777" }}>{"Edit"}</Link>
              </div>
            )
            })}
            <button className="btn btn-dark mt-2" data-bs-toggle="modal" data-bs-target="#addAddress" onClick={()=>handleAddAddressClick('shipping')}>Add New Address</button>
          </div>

        </div>
      </div>
    </div>
  )
}

export default Addresses
